.descr {
  padding: 128px 0;
  &__title {
    width: 411px;
    margin: 0 auto;
    @extend %title;
    @media (max-width: 480px) {
      width: 283px;
      font-size: 20px;
    }
  }
  &__img {
    margin: 0 auto;
    display: block;
    width: 224px;
    @media (min-width: 1200px) {
      width: 206px;
    }
  }

  &__span {
    color: $orange;
  }
  &__text {
    @extend %descrtext;
    max-width: 100%;
    margin: 0 auto;
    margin-top: 28px;
    text-align: center;

    @media (min-width: 375px) {
      max-width: 335px;
    }
    @media (min-width: 1200px) {
      max-width: 486px;
    }
  }
  &__black-text {
    color: $blacktext;
    font-family: Montserrat;
    font-size: 14px;
    font-style: normal;
    font-weight: 600;
    line-height: 18px; /* 128.571% */
    letter-spacing: -0.28px;
    text-transform: uppercase;
    margin-left: 28px;
  }
  &__typeofchoc:nth-child(4) {
    width: 98px;

  }
  &__hover--list {
    background-color: rgb(198, 175, 175);
  }

  &__items {
    position: relative;
    cursor: pointer;
    border-radius: 15px;
    transition: 1s ease-in-out;
    width: 100%;
    height: 270px;
    &:hover .descr__hover{
      transform: rotateY(0deg);


    }
    @media (min-width: 375px) {
      width: 335px;
    }
    @media (min-width: 1200px) {
      width: 270px;
    }
    &:nth-child(1) {
      background: $orange;
      @media (min-width: 768px) {
        order: 1;
      }
      @media (min-width: 1200px) {
        order: 1;
      }
    }
    &:nth-child(2) {
      border: 1px solid $orange;
      background: $white;
      @media (min-width: 768px) {
        order: 3;
      }
      @media (min-width: 1200px) {
        order: 2;
      }
    }
    &:nth-child(3) {
      background: $background-black;

      @media (min-width: 768px) {
        order: 4;
      }
      @media (min-width: 1200px) {
        order: 3;
      }
    }
    &:nth-child(4) {
      border: 1px solid $orange;
      background: $white;
      @media (min-width: 768px) {
        order: 2;
      }
      @media (min-width: 1200px) {
        order: 4;
      }
    }

  }
  &__hover {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    transform: rotateY(90deg);
    transition: 0.3s
    ;
    background: $white;border: 1px solid #FD9222;overflow-y: auto;border-radius: 15px;padding: 28px;
    &::-webkit-scrollbar {
      width: 5px;
    }
    
    &::-webkit-scrollbar-track {
      background-color: #e4e4e4;
      border-radius: 100px;
    }
    
    &::-webkit-scrollbar-thumb {
      background-color: $orange;
      border-radius: 100px;
    }
    &-title {
      color: $blacktext;
      font-family: Montserrat;
      font-size: 18px;
      font-style: normal;
      font-weight: 600;
      line-height: normal;
      letter-spacing: -0.54px;
      text-transform: uppercase;
      margin-bottom: 8px;
    }
    &-text {
      color: $blacktext;
      font-family: Montserrat;
      font-size: 14px;
      font-style: normal;
      font-weight: 400;
      line-height: 18px; /* 128.571% */
      letter-spacing: -0.28px;
      margin-top: 18px;
    }
  }
  &__typeofchoc {
    color: $white;
    font-family: Montserrat;
    font-size: 10px;
    font-style: normal;
    font-weight: 400;
    line-height: 12px; /* 120% */
    border-radius: 35px;
    background: $orange;
padding: 8px 14px;
    border: none;
  }
  &__benefits {
    border-radius: 35px;
    border: 1px solid $orange;
    background: $background;
    color: $blacktext;
    font-family: Montserrat;
    font-size: 10px;
    font-style: normal;
    font-weight: 400;
    line-height: 12px; /* 120% */
    padding: 8px 14px;

  }
  &__white-text {
    color: $white;
    font-family: Montserrat;
    font-size: 14px;
    font-style: normal;
    font-weight: 600;
    line-height: 18px; /* 128.571% */
    letter-spacing: -0.28px;
    text-transform: uppercase;
    margin: 0 0 21px 28px;
  }
  &__hover--item {
    position: relative;
    width: 269px;
    height: 269px;
    cursor: pointer;
    transition: 1s ease-in-out;
    transform-style: preserve-3d;

    transition: 1s ease-in-out;
    -webkit-box-reflect: below 0
      linear-gradient(transparent, transparent, rgba(0, 0, 0, 0.4));

    top: 0;
    left: 0;
    display: none;
    &:hover {
      transform: rotateY(0.5turn);
      .descr__items {
        display: block;
      }
    }
  }
  &__list {
    display: flex;
    justify-content: space-between;
    margin-top: 50px;

    @media (max-width: 1200px) {
      flex-wrap: wrap;
      justify-content: center;
      gap: 20px;
    }

  }
}


.img__wrap {
  padding: 28px 36px 28px 28px;
}
